import Joi from "joi-browser";
import _ from "lodash";
import logger from "../utils/logger";

// 00:00:29  00:00:29.320
const timeRegx = /^\d+:[0-5][0-9]:[0-5][0-9](\.[0-9]{1,3})?$/;

export const schema = {
  startTime: Joi.string()
    .regex(timeRegx)
    .required()
    .min(8)
    .label(""),
  endTime: Joi.string()
    .regex(timeRegx)
    .required()
    .min(8)
    .label(""),
  content: Joi.string()
    .required()
    .max(100)
    .min(1)
    .label(""),
  start: Joi.number(),
  end: Joi.number(),
  length: Joi.number().greater(0)
};

// 
const errorSchema = {
  startTime: " 00:00:29.3208",
  endTime: " 00:00:29.3208",
  content: "1-100",
  start: "",
  end: "",
  length: "0"
};

//    
const labelSchema = {
  startTime: "",
  endTime: "",
  content: "",
  start: "",
  end: "",
  length: ""
};

export function getEditingSubSchema() {
  return {
    startTime: schema.startTime,
    endTime: schema.endTime,
    content: schema.content,
    start: schema.start,
    end: schema.end,
    length: schema.length
  };
}

export function getEditingErrorSchema() {
  return {
    startTime: errorSchema.startTime,
    endTime: errorSchema.endTime,
    content: errorSchema.content,
    start: errorSchema.start,
    end: errorSchema.end,
    length: errorSchema.length
  };
}

const defaultOption = {
  //  
  abortEarly: false,
  //
  allowUnknown: true,
  //
  skipFunctions: true
};

//
export function validate(obj, schema, config) {
  // https://www.jianshu.com/p/e6e277c1fda2
  const option = {
    ...defaultOption,
    ...config
  };
  const result = Joi.validate(obj, schema, option).error;
  // false  
  if (!result) return false;
  // path
  logger.clog("editingSub", result);
  let errorPaths = [];
  result.details.map(d => errorPaths.push(d.path[0]));
  //
  return _.uniq(errorPaths);
}

//
export function validateProperty(name, value, sigleSchema, config) {
  const obj = { [name]: value };
  const option = {
    ...defaultOption,
    abortEarly: true,
    ...config
  };
  const result = Joi.validate(obj, sigleSchema, option).error;
  // true
  return result ? true : false;
}

//errors errorMessages 
export function errors2messages(errors) {
  if (!errors) return [];
  const keys = Object.keys(errors);
  const errorMessages = keys.map(key => {
    return labelSchema[key] + ": " + errors[key];
  });
  if (errorMessages.length === 0) {
    errorMessages.push(" ");
  }
  //
  return errorMessages;
}

//time 00:00:29.320
export function validateTime(time) {
  //true false
  return validateProperty("startTime", time, {
    startTime: schema["startTime"]
  });
}

const validateService = {
  schema,
  getEditingSubSchema,
  getEditingErrorSchema,
  validate,
  validateProperty,
  errors2messages,
  validateTime
};

export default validateService;
